'use client';

import { useEffect, useState } from 'react';
import { usersAPI, clientsAPI, User, Client } from '@/lib/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';

export default function AdminUsersPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [users, setUsers] = useState<User[]>([]);
  const [clients, setClients] = useState<Client[]>([]);
  const [selectedDoctor, setSelectedDoctor] = useState<{ id: string; name: string; patients: any[] } | null>(null);
  const [isLoadingPatients, setIsLoadingPatients] = useState(false);

  useEffect(() => { if (user?.role !== 'admin') router.push('/dashboard'); }, [user, router]);
  useEffect(() => { (async () => { const res = await usersAPI.list(); setUsers(res.data as any); const c = await clientsAPI.list(); setClients(c.data); })(); }, []);

  const clientName = (id?: string) => clients.find(c => c._id === id)?.name || 'â€”';

  const loadDoctorPatients = async (doctorId: string, doctorName: string) => {
    setIsLoadingPatients(true);
    try {
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/patients?doctorId=${doctorId}`, {
        headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }
      });
      const patients = await response.json();
      setSelectedDoctor({ id: doctorId, name: doctorName, patients: patients || [] });
    } catch (error) {
      console.error('Failed to load doctor patients:', error);
    } finally {
      setIsLoadingPatients(false);
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Users</h2>
        <Button onClick={() => router.push('/dashboard/admin/users/new')}>New User</Button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Users Table */}
        <div className="lg:col-span-2">
          <Card>
            <CardHeader>
              <CardTitle>All Users</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="border-b text-left text-sm text-gray-600">
                      <th className="pb-3 font-medium">Name</th>
                      <th className="pb-3 font-medium">Email</th>
                      <th className="pb-3 font-medium">Role</th>
                      <th className="pb-3 font-medium">Client</th>
                      <th className="pb-3 font-medium">Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {users.map(u => (
                      <tr key={u.id} className="border-b hover:bg-gray-50">
                        <td className="py-3 font-semibold text-gray-900">{u.name}</td>
                        <td className="py-3 text-sm text-gray-700">{u.email}</td>
                        <td className="py-3 text-sm text-gray-700">
                          <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                            u.role === 'admin' ? 'bg-purple-100 text-purple-700' :
                            u.role === 'doctor' ? 'bg-blue-100 text-blue-700' :
                            'bg-gray-100 text-gray-700'
                          }`}>
                            {u.role}
                          </span>
                        </td>
                        <td className="py-3 text-sm text-gray-700">{clientName((u as any).client)}</td>
                        <td className="py-3">
                          {u.role === 'doctor' && (
                            <Button
                              onClick={() => loadDoctorPatients(u.id, u.name)}
                              className="text-xs px-3 py-1 bg-blue-600 hover:bg-blue-700 text-white"
                            >
                              View Patients
                            </Button>
                          )}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
                {users.length === 0 && <p className="text-gray-500 text-center py-8">No users</p>}
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Doctor's Patients Panel */}
        <div className="lg:col-span-1">
          <Card className="sticky top-4">
            <CardHeader>
              <CardTitle>
                {selectedDoctor ? `${selectedDoctor.name}'s Patients` : 'Doctor Patients'}
              </CardTitle>
            </CardHeader>
            <CardContent>
              {isLoadingPatients ? (
                <div className="text-center py-8">
                  <div className="animate-spin w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full mx-auto"></div>
                  <p className="text-sm text-gray-500 mt-2">Loading patients...</p>
                </div>
              ) : selectedDoctor ? (
                selectedDoctor.patients.length === 0 ? (
                  <div className="text-center py-8 text-gray-500">
                    <svg className="w-12 h-12 mx-auto mb-2 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
                    </svg>
                    <p className="text-sm font-medium">No patients yet</p>
                  </div>
                ) : (
                  <div className="space-y-3 max-h-[600px] overflow-y-auto">
                    {selectedDoctor.patients.map((patient: any, index: number) => (
                      <div key={patient._id || index} className="p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors">
                        <div className="flex items-start gap-3">
                          <div className="w-8 h-8 bg-gradient-to-br from-green-500 to-emerald-500 rounded-full flex items-center justify-center flex-shrink-0">
                            <span className="text-white text-xs font-bold">
                              {patient.name?.charAt(0).toUpperCase() || '?'}
                            </span>
                          </div>
                          <div className="flex-1 min-w-0">
                            <p className="font-semibold text-gray-900 text-sm truncate">{patient.name}</p>
                            {patient.email && <p className="text-xs text-gray-600 truncate">{patient.email}</p>}
                            {patient.contact && <p className="text-xs text-gray-600">{patient.contact}</p>}
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                )
              ) : (
                <div className="text-center py-12 text-gray-500">
                  <svg className="w-16 h-16 mx-auto mb-3 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197M13 7a4 4 0 11-8 0 4 4 0 018 0z" />
                  </svg>
                  <p className="text-sm">Select a doctor to view their patients</p>
                  <p className="text-xs text-gray-400 mt-1">ðŸ‘ˆ Click "View Patients" button</p>
                </div>
              )}
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
